.main__section{
  position: fixed;
  padding: 20px 50px 50px 50px;
  z-index: 3;
  background: -webkit-gradient(linear, right top, left top, from(rgba(255, 255, 255, .01)), color-stop(rgba(255, 255, 255, 0.8)), color-stop(rgba(255, 255, 255, 1)), to(#fff));
  background: -webkit-linear-gradient(right, rgba(255, 255, 255, .01), rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 1), #fff);
  background: -o-linear-gradient(right, rgba(255, 255, 255, .01), rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 1), #fff);
  background: linear-gradient(to left, rgba(255, 255, 255, .01), rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 1), #fff);
  height: 100%;
}

@media screen and (max-width: 765px) {
  .main__section-st{
    position: fixed;
    z-index: 3;
    background: rgba(0, 0, 0, 0.92);
    height: 100%;
    width: 100%;
    padding: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }

  .main__section-cl{
    position: fixed;
    z-index: 3;
    background: rgba(0, 0, 0, 0.92);
    height: 100%;
    width: 100%;
    padding: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -webkit-transition: all .3s ease-in;
    -o-transition: all .3s ease-in;
    transition: all .3s ease-in;
  }

  .main__section-disabled{
    height: 48px;
    overflow: hidden;
  }
}



