.catalog{
  font-family: 'Open Sans', sans-serif;
}

.catalog__list{
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100%;
  padding: 0;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.catalog__list-item{
  -ms-flex: .2 .2 250px;
      flex: .2 .2 250px;
  height: 300px;
  overflow: hidden;
  border-radius: 5px;
  margin: 20px;
  -webkit-box-shadow: 0 0 3px rgba(0,0,0, 0.5);
          box-shadow: 0 0 3px rgba(0,0,0, 0.5);
}

.catalog{
  width: 100%;
  height: 100%;
  padding-top: 80px;
}

.item__info-button{
  display: block;
  width: 120px;
  height: 45px;
  background: #50e3c2;
  border-radius: 5px;
  line-height: 45px;
  margin: 0;
  text-decoration: none;
  color: #000;
}

.catalog__list-item-holder{
  height: 100%;
  width: 100%;
  position: relative;
}

.item__image{
  height: 100%;
  background: center center no-repeat;
  background-size: cover;
}

.item__info{
  position: absolute;
  left: 0px;
  top: 0px;
  -webkit-transition: all .3s linear;
  -o-transition: all .3s linear;
  transition: all .3s linear;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
}

.item__info:hover{
  background: rgba(0, 0, 0, .6);
}

.item__info:hover .item__info-holder{
  border: 1px solid rgba(255, 255, 255, .6);
}

.item__info-holder{
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: calc(100% - 40px);
  height: calc(100% - 40px);
  margin: 20px;
  border-radius: 5px;
  -webkit-transition: all .3s linear;
  -o-transition: all .3s linear;
  transition: all .3s linear;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow: hidden;
}

.item__info-holder-swipe{
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  bottom: 0px;
  opacity: 0;
  -webkit-transition: all .4s linear;
  -o-transition: all .4s linear;
  transition: all .4s linear;
}

.item__info:hover .item__info-holder-swipe{
  bottom: 50%;
  opacity: 1;
  -webkit-transform: translateY(50%);
      -ms-transform: translateY(50%);
          transform: translateY(50%);
}

.item__info-name{
  margin: 0;
  font-size: 23px;
  color: rgba(255, 255, 255, 0.9);
}

.item__info-price{
  font-size: 20px;
  color: rgba(255, 255, 255, 0.9);
}

.reduce__animation{
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}

@media screen and (max-width: 765px) {
  .catalog__list-item{
    width: 100%;
    max-width: 300px;
    -ms-flex: .2 .2 350px;
        flex: .2 .2 350px;
    background: red;
    margin: 10px 0;
  }

  .catalog__list{
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .item__info-holder{
    overflow: visible;
    margin: 0;
    width: 100%;
    height: 50px;
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
  }

  .item__info{
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
    position: relative;
    height: 50px;
  }

  .item__info-holder-swipe{
    position: relative;
    opacity: 1;
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
  }

  .item__info:hover .item__info-holder{
    border: none;
  }

  .item__info:hover{
    background: none;
  }

  .item__info:hover .item__info-holder-swipe{
    bottom: 0;
    opacity: none;
    -webkit-transform: none;
        -ms-transform: none;
            transform: none;
  }

  .item__info-holder-swipe{
    width: 100%;
  }

  .catalog__list-item-holder{
    height: 350px;
  }

  .item__info-name{
    display: none;
  }

  .item__info-price{
    display: none;
  }

  .item__image{
    height: 300px;
  }

  .item__info-button{
    height: 50px;
    border-top-right-radius: 0;
    border-top-left-radius: 0;
    width: 100%;
  }
}



